%list {
  padding: 0;
  margin: 0;

  font-style: normal;
  list-style-type: none;
  text-decoration: none;
}
%link {
  text-decoration: none;
}

%vissually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.overflow {
  overflow: hidden;
}
